body {
  margin: 0 auto;
  background-color: #000;
  background-image: url(../images/pattern.jpg);
  background-size: 70px;
  font-family: "Trebuchet MS", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 13px;
  line-height: 16px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 320px;
  height: 100vh;
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
ul {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}

main {
  margin-top: -29px;
}

.color-blue {
  color: #1698d9;
}

.btn-buy {
  position: relative;
  color: #1698d9;
  font-weight: 700;
  cursor: pointer;
  transition: color .2s ease-in-out;
  touch-action: manipulation;
}

.btn-buy::after {
  position: absolute;
  content: '';
  display: block;
  left: 1px;
  width: 100%;
  bottom: 0;
  height: 1px;
  background-image: repeating-linear-gradient(to right, #1698d9 0, #1698d9 3px, transparent 3px, transparent 5px);
  z-index: 1;
  transition: background-image .2s ease-in-out;
}

.btn-buy:hover {
  color: #2ea8e6;
}

.btn-buy:hover::after {
  background-image: repeating-linear-gradient(to right, #2ea8e6 0, #2ea8e6 3px, transparent 3px, transparent 5px);
}

.catalog {
  display: flex;
  justify-content: space-between;
  opacity: 0;
  visibility: hidden;
  transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
}

.catalog--visible {
  opacity: 1;
  visibility: visible;
}

.container {
  width: 1280px;
  padding: 0 80px;
}

.main-title {
  font-size: 36px;
  line-height: 43px;
  font-family: 'exo2', sans-serif;
  font-weight: 300;
  color: #fff;
  text-align: center;
  text-shadow: 0 1px 0 #000;
  margin-bottom: 23px;
}

.product {
  position: relative;
  width: 320px;
  overflow: hidden;
}

.product__bottom-text {
  font-size: 13px;
  line-height: 16px;
  text-align: center;
  text-shadow: 0 1px 0 #000;
  margin-top: 14px;
}

.product__label {
  position: absolute;
  right: 11px;
  bottom: 15px;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #1698d9;
  font-size: 0;
  line-height: 73px;
  color: #fff;
  text-align: center;
  z-index: 1;
  transition: background-color .2s ease-in-out;
}

.product__label-value {
  display: inline-block;
  vertical-align: bottom;
  font-size: 21px;
  line-height: 22px;
}

.product__label-value span {
  font-size: 42px;
}

.product {
  text-align: right;
}

.product__header,
.product__body {
  position: relative;
  background-color: #fff;
}

.product__header {
  display: inline-block;
  vertical-align: top;
  text-align: left;
  width: 275px;
  max-height: 45px;
  overflow: hidden;
  padding-top: 17px;
  padding-bottom: 5px;
  padding-left: 4px;
  border-top: 4px solid #1698d9;
  border-right: 4px solid #1698d9;
  border-top-right-radius: 12px;
  transition: border-color .2s ease-in-out;
}

.product__header::after {
  position: absolute;
  content: '';
  display: block;
  left: 0;
  right: 0;
  bottom: -5px;
  height: 10px;
  width: 100%;
  background-color: #fff;
  z-index: -1;
}

.product__header-text {
  font-size: 16px;
  line-height: 19px;
  color: #666666;
  max-height: 19px;
  overflow: hidden;
}

.product__header-text--reject {
  display: none;
  color: #e62e7a;
}

.product__body {
  text-align: left;
  padding-top: 1px;
  padding-bottom: 290px;
  overflow: hidden;
  padding-left: 45px;
  padding-right: 45px;
  border-right: 4px solid #1698d9;
  border-left: 4px solid #1698d9;
  transition: border-color .2s ease-in-out;
}

.product__title,
.product__subtitle {
  font-weight: 700;
  color: #000000;
}

.product__title {
  font-size: 48px;
  line-height: 54px;
  margin-left: -3px;
}

.product__subtitle {
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 13px;
}

.product__desc {
  position: relative;
  font-size: 14px;
  line-height: 16px;
  color: #666666;
  z-index: 2;
}

.product__img {
  position: absolute;
  bottom: -88px;
  left: -28px;
  z-index: 1;
}

.product__item {
  position: relative;
  overflow: hidden;
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
}

.product__item::before,
.product__item::after {
  position: absolute;
  content: '';
  display: block;
  bottom: 0;
  width: 20px;
  height: 20px;
  background-color: transparent;
  z-index: 2;
  transition: border-color .2s ease-in-out;
}

.product__item::before {
  left: 0;
  border-left: 4px solid #1698d9;
  border-bottom: 4px solid #1698d9;
  border-bottom-left-radius: 12px;
}

.product__item::after {
  right: 0;
  border-right: 4px solid #1698d9;
  border-bottom: 4px solid #1698d9;
  border-bottom-right-radius: 12px;
}

.product__item--active {
  cursor: pointer;
  touch-action: manipulation;
}

.product__item-before,
.product__item-after {
  position: absolute;
  content: '';
  display: block;
}

.product__item-before {
  top: 9px;
  left: 12px;
  border-left: 4px solid #1698d9;
  transform: rotate(45deg);
  width: 75px;
  height: 75px;
  background-color: #fff;
  z-index: -1;
  transition: border-color .2s ease-in-out;
}

.product__item-after {
  bottom: 0;
  height: 4px;
  left: 18px;
  right: 18px;
  background-color: #1698d9;
  z-index: 2;
  transition: background-color .2s ease-in-out;
}

.product__item--active:hover .product__header {
  border-top-color: #2ea8e6;
}

.product__item--active:hover .product__header,
.product__item--active:hover .product__body,
.product__item--active:hover::before,
.product__item--active:hover::after {
  border-right-color: #2ea8e6;
}

.product__item--active:hover .product__body,
.product__item--active:hover .product__item-before,
.product__item--active:hover::before,
.product__item--active:hover::after {
  border-left-color: #2ea8e6;
}

.product__item--active:hover .product__item-after,
.product__item--active:hover .product__label {
  background-color: #2ea8e6;
}

.product__item--active:hover::before,
.product__item--active:hover::after {
  border-bottom-color: #2ea8e6;
}

.product__item--selected .product__header,
.product__item--selected:hover .product__header {
  border-top-color: #d91667;
}

.product__item--selected .product__header,
.product__item--selected .product__body,
.product__item--selected::before,
.product__item--selected::after,
.product__item--selected:hover .product__header,
.product__item--selected:hover .product__body,
.product__item--selected:hover::before,
.product__item--selected:hover::after {
  border-right-color: #d91667;
}

.product__item--selected .product__body,
.product__item--selected .product__item-before,
.product__item--selected::before,
.product__item--selected::after,
.product__item--selected:hover .product__body,
.product__item--selected:hover .product__item-before,
.product__item--selected:hover::before,
.product__item--selected:hover::after {
  border-left-color: #d91667;
}

.product__item--selected .product__item-after,
.product__item--selected .product__label,
.product__item--selected:hover .product__item-after,
.product__item--selected:hover .product__label {
  background-color: #d91667;
}

.product__item--selected::before,
.product__item--selected::after,
.product__item--selected:hover::before,
.product__item--selected:hover::after {
  border-bottom-color: #d91667;
}

.product__item--selected.hover-enabled:hover .product__header {
  border-top-color: #e62e7a;
}

.product__item--selected.hover-enabled:hover .product__header,
.product__item--selected.hover-enabled:hover .product__body,
.product__item--selected.hover-enabled:hover::before,
.product__item--selected.hover-enabled:hover::after {
  border-right-color: #e62e7a;
}

.product__item--selected.hover-enabled:hover .product__body,
.product__item--selected.hover-enabled:hover .product__item-before,
.product__item--selected.hover-enabled:hover::before,
.product__item--selected.hover-enabled:hover::after {
  border-left-color: #e62e7a;
}

.product__item--selected.hover-enabled:hover .product__item-after,
.product__item--selected.hover-enabled:hover .product__label {
  background-color: #e62e7a;
}

.product__item--selected.hover-enabled:hover::before,
.product__item--selected.hover-enabled:hover::after {
  border-bottom-color: #e62e7a;
}

.product__item--selected.hover-enabled:hover .product__header-text {
  display: none;
}

.product__item--selected.hover-enabled:hover .product__header-text--reject {
  display: block;
}

.product--disabled .product__header {
  border-top-color: #b3b3b3;
}

.product--disabled .product__header,
.product--disabled .product__body,
.product--disabled .product__item::before,
.product--disabled .product__item::after {
  border-right-color: #b3b3b3;
}

.product--disabled .product__body,
.product--disabled .product__item-before,
.product--disabled .product__item::before,
.product--disabled .product__item::after {
  border-left-color: #b3b3b3;
}

.product--disabled .product__item-after,
.product--disabled .product__label {
  background-color: #b3b3b3;
}

.product--disabled .product__item::before,
.product--disabled .product__item::after {
  border-bottom-color: #b3b3b3;
}

.product--disabled .product__header-text,
.product--disabled .product__title,
.product--disabled .product__subtitle,
.product--disabled .product__desc {
  color: #b3b3b3;
}

.product--disabled .product__header-text,
.product--disabled .product__title,
.product--disabled .product__subtitle,
.product--disabled .product__desc,
.product--disabled .product__img {
  opacity: .5;
}

.product--disabled .product__bottom-text {
  color: #ffff66;
}

@media (max-width: 1279px) {
  .container {
    width: 1024px;
    padding: 0 25px;
  }

  .product {
    width: 304px;
  }

  .product__header {
    width: 259px;
  }
}

@media (max-width: 1023px) {
  body {
    height: auto;
  }

  main {
    margin-top: 0;
    padding-top: 34px;
    padding-bottom: 20px;
  }

  .catalog {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .container {
    width: 768px;
    padding: 0 24px;
  }

  .main-title {
    font-size: 32px;
    line-height: 39px;
  }

  .product {
    width: 320px;
    margin-bottom: 36px;
  }

  .product__header {
    width: 275px;
  }
}

@media (max-width: 767px) {
  main {
    padding-top: 27px;
    padding-bottom: 10px;
  }

  .catalog {
    display: block;
  }

  .container {
    width: 320px;
    padding: 0 20px;
  }

  .main-title {
    font-size: 28px;
    line-height: 32px;
  }

  .main-title {
    margin-bottom: 25px;
  }

  .product {
    width: 280px;
  }

  .product__label {
    width: 72px;
    height: 72px;
    line-height: 65px;
  }

  .product__label-value {
    font-size: 19px;
    line-height: 20px;
  }

  .product__label-value span {
    font-size: 36px;
  }

  .product__header {
    width: 235px;
  }

  .product__header-text {
    font-size: 15px;
    line-height: 18px;
  }

  .product__body {
    padding-bottom: 270px;
    height: auto !important;
  }

  .product__title {
    font-size: 42px;
    line-height: 48px;
  }

  .product__subtitle {
    font-size: 20px;
    line-height: 25px;
  }

  .product__desc {
    font-size: 13px;
    line-height: 15px;
  }

  .product__img {
    bottom: -98px;
    left: -38px;
  }
}